
















































































import { Component, Vue } from 'vue-property-decorator';
import {HttpService} from "@/services/ApiService";
import { Dboy } from '@/models/service';


@Component
export default class Mensajeros extends Vue {
  public urlImgDboy = 'https://todi-upload.s3.amazonaws.com/upload/dboys/';

  public search = ''
  public deliveries: Dboy[] = []

  setDeliveries(data: any){
    console.log(data)
    this.deliveries = data.data;
  }

  setItem(data:any){
    let delivery: Dboy = data.data;
    let index = this.deliveries.findIndex(e => e.id == delivery.id);
    if(index != undefined && index > -1){
      //this.deliveries.splice(index, 1, delivery)
      console.log(index);
      this.$set(this.deliveries, index, delivery)
    }
  }

  changeStatus(id: number){
    HttpService.get(`mensajeros/${id}/cambiar/estatus` ,
        this.setItem)
  }

  changeVerified(id: number){
    HttpService.get(`mensajeros/${id}/cambiar/verificar` ,
        this.setItem)
  }

  addSaldoDboy(id: number, amount: number){
    HttpService.post(`mensajeros/${id}/cambiar/wallet` ,
        {'wallet': amount},
        this.setItem)
  }

  addSaldo(id: string, idDboy: number){
    let input = document.getElementById(id);

    //@ts-ignore
    if(input?.value >= 10000){
      //@ts-ignore
      console.log(input?.value)
      //@ts-ignore
      this.addSaldoDboy(idDboy, input?.value)
      //@ts-ignore
      input?.value = "";
    }
  }


  getDeliveries(){
    HttpService.get(`repartidores?search=${this.search}` ,
        this.setDeliveries)
  }

  created(){
    this.getDeliveries()
  }

}
