
import {Service} from "@/services/Service";

class ApiService extends  Service{


    constructor() {
        super();
    }

    get(path:string, callback:Function) {
        return this.service.get(path).then(
            (response) => callback(response.data)
        );
    }

    delete(path:string, callback:Function) {
        return this.service.delete(path).then(
            (response) => callback(response.data)
        );
    }

    patch(path:any, payload:any, callback:Function) {
        return this.service.request({
            method: 'PATCH',
            url: path,
            responseType: 'json',
            data: payload
        }).then((response) => callback(response.status, response.data));
    }

    post(path:string, payload:Object, callback:Function) {
        return this.service.request({
            method: 'POST',
            url: path,
            responseType: 'json',
            data: payload
        }).then((response) => callback(response.data));
    }

    put(path:string, payload:Object, callback:Function) {
        return this.service.request({
            method: 'PUT',
            url: path,
            responseType: 'json',
            data: payload
        }).then((response) => callback(response.data));
    }



    test() {
        console.log(this.$vue);
        console.log(this.$router);
    }
}

export const HttpService = new ApiService();
